<template>
  <div id="adfailed">
    <head-top title="Failed To Add Device" goBack = 'true'></head-top>
    <img src="../../../assets/addevice/Don'tconnect@3x.png" alt="">
    <span>Mobile phone does not connect to Wifi network</span>
    <span>Please go to the phone set up page for WIFI connection</span>
    <div class="button" @click="$router.go(-1)">
      Ok
    </div>
  </div>
</template>
<script>
import headTop from '../../../components/common/head'

export default {
  data () {
    return {}
  },
  components: {
    headTop
  }
}
</script>
<style lang="less" scoped>
#adfailed{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  img{
    .wh(536px, 227px);
    margin-top: 87px;
    align-self: center;
    display: block;
  }
  span:nth-of-type(1) {
    width: 536px;
    font-size: 34px;
    line-height: 43px;
    align-self: center;
    margin-top: 40px;
  }
  span:nth-of-type(2) {
    font-size: 23px;
    line-height: 33px;
    color: #999999;
    align-self: center;
    margin-top: 60px;
    width: 469px;

  }
  .button{
    margin-top: 46%;
  }
}
</style>



